<template>
  <div class="subject" v-if="details" ref="subject">
    <div class="details">
      <h3 ref="h3">{{details.data.chapter.title}}</h3>
      <p id="qqq" ref="p" @click="clickShow">{{lineFeed}}</p>
      <p id="qqqq"></p>
    </div>
    <div class="select">
      <span @click="topOne">上一章</span>
      <span @click="catalog">目录</span>
      <span @click="bottonOne">下一章</span>
    </div>

    <transition name="top">
      <div class="clickTop" v-show="show">
        <div class="left">
          <img src="../../public/img/detail-page03.png">
          <span @click="goBack">返回</span>
        </div>
        <span class="title">{{details.data.chapter.title}}</span>
        <div class="right">
          <img @click="clickImg" src="../../public/img/instruction01.png">
          <transition name="top">
            <ul v-show="ulShow">
              <li>
                <i></i>
                <img src="../../public/img/instruction02.png">
                <span @click="home">首页</span>
              </li>
              <li>
                <img src="../../public/img/instruction03.png">
                <span>书架</span>
              </li>
              <li>
                <img src="../../public/img/instruction04.png">
                <span @click="goDetails">详情</span>
              </li>
            </ul>
          </transition>
        </div>
      </div>
    </transition>

    <transition name="bottom">
      <div class="clickBottom" v-show="show">
        <div class="typeface">
          <span @click="subSize" :class="psize <= 16? 'active':''">Aa-</span>
          <span class="strip">
            <p ref="strip"></p>
          </span>
          <span @click="addSize" :class="psize >= 24? 'active':''">Aa+</span>
        </div>
        <div class="complexion">
          <span @click="defaultColor" :class="activeColor? 'active' : ''">默认</span>
          <span @click="night" :class="nightColor? 'active' : ''">夜间</span>
          <span @click="eyeshield" :class="eyeshieldColor? 'active' : ''">护眼</span>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: "Read",
  //item: 本章数据
  //id: 本书的id号
  //datas: 本书的所有章节数据
  props: ["item", "id", "name", "datas"],
  data() {
    return {
      //小说内容API
      url: "",
      //本章内容
      details: "",
      copyItem: "",
      activeColor: true,
      nightColor: false,
      eyeshieldColor: false,
      h3size: 22,
      psize: 18,
      pWidth: 25,
      show: false,
      ulShow: false,
      number: 0,
      index: 0
    };
  },
  computed: {
    lineFeed() {
      let value =
        this.details.data.chapter.body.length >
        this.details.data.chapter.cpContent.length
          ? this.details.data.chapter.body.replace(/。/g, "。&lt;br&gt;")
          : this.details.data.chapter.cpContent.replace(/。/g, "kk");
      return value;
      console.log("章节内容", this.details);
    }
  },
  methods: {
    catalog() {
      console.log("name==>", this.name);
      this.$router.push({
        path: "/Section",
        query: { id: this.id, name: this.name }
      });
    },
    topOne() {
      if(this.index == 0) {
        this.index = 0
      } else {
        this.url = "/chapterup/chapter/" + this.datas[this.index - 1].link;
        this.axios.get(this.url).then(response => {
        this.details = response;
        if(this.index === 0) {
          this.index = 0
        } else {
          this.index -- 
        }
        //回到顶部
        window.scrollTo(0,0); 
        });
      }
    },
    bottonOne() {
      if(this.number === 0) {
        this.url = "/chapterup/chapter/" + this.datas[this.item.order].link;
        this.axios.get(this.url).then(response => {
        this.details = response;
        this.number = 1
        this.index = this.item.order
        window.scrollTo(0,0); 
        });
      } else {
        this.url = "/chapterup/chapter/" + this.datas[this.index + 1].link;
        this.axios.get(this.url).then(response => {
        this.details = response;
        this.index ++ 
        window.scrollTo(0,0); 
        });
      }
      
    },
    //背景颜色
    defaultColor() {
      this.activeColor = true;
      this.eyeshieldColor = false;
      this.nightColor = false;
      this.$refs.subject.style.background = "rgba(202, 221, 216, 0.897)";
    },
    night() {
      this.nightColor = true;
      this.activeColor = false;
      this.eyeshieldColor = false;
      this.$refs.subject.style.background = "#000";
    },
    eyeshield() {
      this.eyeshieldColor = true;
      this.activeColor = false;
      this.nightColor = false;
      this.$refs.subject.style.background = "rgba(184, 235, 102, 0.897)";
    },
    //字体大小
    addSize() {
      if (this.psize < 24 && this.h3size < 28) {
        this.$refs.h3.style.fontSize = this.h3size + 2 + "px";
        this.$refs.p.style.fontSize = this.psize + 2 + "px";
        this.h3size = this.h3size + 2;
        this.psize = this.psize + 2;
        this.$refs.strip.style.width = this.pWidth + 25 + "%";
        this.pWidth = this.pWidth + 25;
      }
      console.log("psize==>", this.psize);
    },
    subSize() {
      if (this.psize > 16 && this.h3size > 20) {
        this.$refs.h3.style.fontSize = this.h3size - 2 + "px";
        this.$refs.p.style.fontSize = this.psize - 2 + "px";
        this.h3size = this.h3size - 2;
        this.psize = this.psize - 2;
        this.$refs.strip.style.width = this.pWidth - 25 + "%";
        this.pWidth = this.pWidth - 25;
      }
    },
    goBack() {
      this.$router.go(-1);
    },
    home() {
      this.$router.push("/");
    },
    goDetails() {
      console.log(this.name);
      this.$router.push({ path: "/Details", query: { name: this.name } });
    },
    clickShow() {
      this.show = !this.show;
    },
    clickImg() {
      this.ulShow = !this.ulShow;
    }
  },
  created() {
    // console.log("1111111111",document.getElementById("qqq").innerText.replace(/。/g,"/n"))
    console.log("name==>", this.name);
    console.log("item==>", this.item);
    // if (this.link.indexOf("cv=") > -1) {
    //   console.log("有");
    //   let a = this.link.substr(-13, 13);
    //   let b = String(parseInt(this.link.substr(-13, 13)) + 5);
    //   this.nextDetails = this.link.split(a).join(b);
    // } else {
    //   let a = this.link.substr(-12, 7);
    //   let b = String(parseInt(this.link.substr(-12, 7)) + 1);
    //   this.nextDetails = this.link.split(a).join(b);
    // }

    // console.log("nextDetails==>", this.nextDetails);

    this.url = "/chapterup/chapter/" + this.item.link;
    console.log(this.url);
    this.axios.get(this.url).then(response => {
      this.details = response;
      console.log("11",this.details);
      
    });
  }
};
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
@import "../../public/sass/mixin";

.subject {
  margin-top: -100px;
  position: relative;
  top: 0;
  left: 0;
  background: rgba(202, 221, 216, 0.897);

  .details {
    padding: 0px 23px 0px 25px;
    width: calc(100% - 48px);
    overflow: hidden;

    h3 {
      display: block;
      margin-right: 2px;
      height: 100px;
      line-height: 100px;
      text-align: center;
      font-size: 22px;
      color: #fff;
      background: #9b2b1d;
    }

    p {
      line-height: 30px;
      text-indent: 2em;
      font-size: 18px;
      color: #5c5d58;
    }
  }

  .select {
    display: flex;
    height: 50px;

    span {
      flex: 1;
      height: 50px;
      line-height: 50px;
      text-align: center;
      color: #5c5d58;
    }
  }
}

.subject .clickTop {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 44px;
  background: rgba(50, 51, 52, 0.9);

  .left {
    flex: 0 0 60px;
    width: 60px;
    height: 100%;
    line-height: 44px;

    img {
      margin-left: 5px;
      transform: translateY(15%);
      width: 20px;
    }

    span {
      vertical-align: top;
      font-size: 12px;
      color: #fff;
    }
  }

  .right {
    flex: 0 0 60px;
    width: 60px;
    height: 100%;
    line-height: 44px;
    text-align: center;

    img {
      width: 15px;
    }

    ul {
      position: fixed;
      right: 0;
      top: 54px;
      width: 90px;
      height: 90px;
      border-radius: 3px;
      background: rgb(50, 51, 52);
    }

    ul li {
      position: relative;
      width: 100%;
      height: 30px;
      line-height: 30px;
      @include border-bottom(#222);
    }

    ul li i {
      display: inline-block;
      position: absolute;
      top: -8px;
      left: 66px;
      border: 4px solid rgb(50, 51, 52);
      border-color: transparent transparent rgb(50, 51, 52) transparent;
    }

    ul li img {
      width: 12px;
      height: 12px;
    }

    ul li span {
      margin-left: 10px;
      font-size: 12px;
      color: #fff;
    }
  }

  .title {
    flex: 1;
    height: 100%;
    line-height: 44px;
    text-align: center;
    font-size: 12px;
    font-weight: bold;
    color: #fff;
  }
}

.subject .clickBottom {
  position: fixed;
  left: 0;
  bottom: 0;
  padding: 20px;
  width: calc(100% - 40px);
  height: 80px;
  background: rgba(50, 51, 52, 0.9);

  .typeface {
    display: flex;
    padding-bottom: 20px;
    width: 100%;
    height: 30px;
    line-height: 30px;

    span {
      flex: 0 0 30px;
      display: inline-block;
      width: 30px;
      height: 30px;
      line-height: 30px;
      font-size: 19px;
      color: #fff;
    }

    span.active {
      color: #888;
    }

    .strip {
      position: relative;
      margin: 14px 20px 0;
      flex: 1;
      height: 2px;
      background: #b2b2b2;
    }

    .strip p {
      position: absolute;
      top: 0;
      left: 0;
      width: 25%;
      height: 100%;
      background: #b93221;
    }
  }
}

.subject .complexion {
  display: flex;
  width: 100%;
  height: 30px;

  span {
    margin: 0 10px;
    display: inline-block;
    flex: 1;
    font-size: 14px;
    text-align: center;
    line-height: 30px;
    border: 1px solid #535353;
    color: #fff;
  }

  span.active {
    border: 1px solid #b93221;
    color: #b93221;
  }
}

.top-enter-active,
.top-leave-active {
  transition: all 0.2s ease-out;
}
.top-enter,
.top-leave-to {
  transform: translateY(-100%);
}
.top-enter-to,
.top-leave {
  transform: translateY(0);
}

.bottom-enter-active,
.bottom-leave-active {
  transition: all 0.2s ease-out;
}
.bottom-enter,
.bottom-leave-to {
  transform: translateY(100%);
}
.bottom-enter-to,
.bottom-leave {
  transform: translateY(0);
}
</style>
