<template>
  <div class="details" >
    <div class="headline">
      <div class="go-back">
        <img src="../../public/img/detail-page03.png" alt>
        <span @click="upOne">返回</span>
      </div>
      <span class="book-details">书籍详情</span>
      <div class="homepage">
        <img src="../../public/img/detail-page02.png" @click="homepage">
      </div>
    </div>

    <div class="message">
      <div class="img">
        <img :src="setDatum(name.cover)">
      </div>
      <div class="right">
        <span class="name">{{name.title}}</span>
        <div>
          <span class="author">{{name.author}}</span> |
          <span class="genre">{{name.minorCate}}</span>
        </div>
        <div :v-model="name.tags && name.tags.length > 4">
          <p>标签:</p>
          <span class="label" v-for="(item, index) in name.tags" :key="index">{{item}}、</span>
        </div>
      </div>
    </div>

    <div class="btn">
      <div class="left">
        <span>加入书架</span>
      </div>
      <div class="right">
        <span @click="BeginReading">开始阅读</span>
      </div>
    </div>

    <div class="information">
      <div class="moods">
        <span>追人气</span>
        <span class="number">{{name.latelyFollower}}</span>
      </div>
      <div class="retention-rate">
        <span>读者留存率</span>
        <span class="number">{{name.retentionRatio}}</span>
      </div>
      <div>
        <span class="day-more">日更字数/天</span>
        <span class="number">0</span>
      </div>
    </div>

    <div class="synopsis">
      <span ref="span">{{name.shortIntro}}</span>
      <img :src="publicPath+'img/'+imgs" @click="active">
    </div>

    <div class="newest" @click="goSection(name._id)">
      <p>目录</p>
      <span>{{name.lastChapter}}</span>
      <img src="../../public/img/arrows-right.png">
    </div>

    <div class="discuss">
      <div class="head">
        <p>热门书评</p>
        <span>更多书评</span>
      </div>
      <div class="content">
        <span>暂无评论 ~</span>
      </div>
    </div>

    <div class="interest">
      <div class="interest-head">
        <i></i>
        <p>你可能感觉趣</p>
        <span @click="goInterested">更多</span>
      </div>
      <div class="interest-book">
        <div class="book" v-for="(item, index) in disposeUrlOther" :key="index" @click="change(item)">
          <img :src="setDatum(item.cover)">
          <span>{{item.title}}</span>
        </div>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
const url =
  "/api/book/by-categories?gender=male&type=hot&major=玄幻&minor=&start=0&limit=3";

export default {
  name: "Details",
  props: ["name"],
  data() {
    return {
      item: '',
      publicPath: process.env.BASE_URL,
      items: [],
      imgs: "arrows-bottom.png",
      //对应的类型路径
      url: "",
      // 处理后的数据
      disposeUrlOther: "",
      id: "",
    };
  },
  computed: { 

  },
  methods: {
    setDatum(url) {
      //图片路径处理
      if(url) {
        let imgurl = url.replace("/agent/", "");
        imgurl = unescape(imgurl);
        return imgurl;
      }
    },
    active() {
      if (!this.$refs.span.classList.contains("active")) {
        this.$refs.span.classList.add("active");
        this.imgs = "arrows-top.png";
      } else {
        this.$refs.span.classList.remove("active");
        this.imgs = "arrows-bottom.png";
      }
    },
    upOne() {
      // this.$router.push({path: '/'})
      this.$router.go(-1)
    },
    goSection(id) {
      this.$router.push({path: '/Section', query: {id: id, name: this.name}})
    },
    homepage() {
      this.$router.push({path: "/"})
    },
    BeginReading() {
      // this.$router.push({path: "/Read", query: {item: item, id: this.id, name: this.name, datas: this.chapter.data.chapters}})
    },
    change(item) {
      console.log("item==>", item)
      this.$router.push({path: '/Details', query:{name:item}})
    },
    goInterested() {
      let type 
      if(this.name.majorCate) {
        type = this.name.majorCate
      } else {
        type = this.name.cat
      }
      console.log(type)
      this.$router.push({path: '/Interested', query:{type : type, name: this.name}})
    },
  },
  created() {
    console.log(this.name)
    this.id = this.name._id
    this.item = this.$route.query.name
    if(this.name.majorCate) {
      this.url = "/api/book/by-categories?gender=male&type=hot&major="+this.name.majorCate+"&minor=&start=0&limit=5"
    } else {
      this.url = "/api/book/by-categories?gender=male&type=hot&major="+this.name.cat+"&minor=&start=0&limit=5"
    }
    
    //数据请求
    this.axios.get(this.url).then(
      response => {
        //过滤本书的数据
        let title = this.name.title
        this.disposeUrlOther = response.data.books.filter(function(element) {
           return element.title !== title
        })
        //取4本书
          this.disposeUrlOther = this.disposeUrlOther.filter(function(element, index) {
           return index <= 3
        })
      },
      err => {
        alert(err);
      }
    );
  },
  beforeRouteEnter(to, from, next) {
		console.log(from)
		next()
	}
};
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
@import "../../public/sass/mixin";

.details {
  margin-top: -100px;
  z-index: 999;
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 120%;
  background: #fff;
}
.details .headline {
  display: flex;
  width: 100%;
  height: 44px;
  background: #b93321;

  .go-back {
    flex: 0 0 60px;
    width: 60px;
    height: 100%;
    line-height: 44px;

    img {
      margin-left: 10px;
      width: 15px;
    }

    span {
      display: inline-block;
      margin-top: -3px;
      vertical-align: top;
      font-size: 12px;
      font-weight: 400;
      color: #fff;
    }
  }

  .book-details {
    flex: 1;
    text-align: center;
    line-height: 44px;
    font-size: 14px;
    color: #fff;
  }

  .homepage {
    flex: 0 0 50px;
    width: 50px;
    height: 100%;
    text-align: center;
    line-height: 44px;

    img {
      width: 15px;
    }
  }
}

.message {
  display: flex;
  padding: 20px 20px 0 20px;
  width: calc(100%-40px);
  height: 128px;

  .img {
    flex: 0 0 80px;
    width: 80px;
    height: 100px;

    img {
      width: 100%;
      height: 100%;
    }
  }

  .right {
    flex: 1;
    margin-left: 10px;
    height: 100%;

    .name {
      display: inline-block;
      width: 100%;
      height: 26px;
      line-height: 26px;
      font-size: 16px;
      font-weight: 400;
    }

    div {
      width: 100%;
      height: 25px;
      line-height: 25px;
      font-size: 12px;
    }

    div .author {
      margin-right: 10px;
      color: #b93321;
    }

    div .genre {
      margin-left: 10px;
      color: #999;
    }

    p {
      display: inline-block;
      margin-right: 5px;
      color: #999;
    }

    .label {
      color: orange;
    }
  }
}

.btn {
  display: flex;
  padding: 0 20px 0 20px;
  width: calc(100%-40px);
  height: 64px;
  @include border-bottom(#ebebeb);

  .left,
  .right {
    flex: 0 0 150px;
    margin-right: 17px;
    width: 15px;
    height: 44px;
    line-height: 44px;
    text-align: center;
    border: 1px solid #b93321;
    box-sizing: border-box;
    border-radius: 2px;
    font-size: 16px;
    background: #fff;
    color: #b93321;
  }

  .right {
    margin-left: 17px;
    border: 1px solid #fff;
    background: #b93321;
    color: #fff;
  }
}

.information {
  display: flex;
  padding: 20px 0;
  width: 100%;
  height: 44px;
  @include border-bottom(#ebebeb);

  div {
    flex: 1;
    height: 44px;
    text-align: center;
    line-height: 1;

    span {
      display: block;
      font-size: 12px;
      color: #999;
    }

    .number {
      margin-top: 10px;
      font-size: 16px;
      color: #222;
    }
  }
}

.synopsis {
  position: relative;
  padding: 20px;
  @include border-bottom(#ebebeb);

  span {
    display: inline-block;
    width: 100%;
    height: 70px;
    line-height: 22px;
    word-break: break-all;
    text-overflow: ellipsis;
    overflow: hidden;
    font-size: 13px;
  }

  span.active {
    word-break: normal;
    text-overflow: clip;
    overflow: visible;
  }

  img {
    position: absolute;
    bottom: 28px;
    right: 8px;
    width: 12px;
    height: 12px;
  }
}

.newest {
  display: flex;
  padding: 20px 10px;
  @include border-bottom(#ebebeb);

  p {
    display: inline-block;
    margin-left: 10px;
    flex: 0 0 50px;
    height: 20px;
    line-height: 20px;
    font-size: 14px;
    color: #333;
  }

  span {
    flex: 1;
    margin-right: 5px;
    text-align: right;
    line-height: 20px;
    font-size: 12px;
    word-break: break-all;
    text-overflow: ellipsis;
    overflow: hidden;
    color: #999;
  }

  img {
    margin-top: 5px;
    flex: 0 0 12px;
    width: 12px;
    height: 12px;
  }
}

.discuss {
  padding-bottom: 10px;
  width: 100%;
  height: 92px;
  background: #f4f4f4;

  .head {
    position: relative;
    width: 100%;
    height: 40px;
    line-height: 40px;
    background: #fff;

    p {
      display: inline-block;
      padding-left: 20px;
      font-size: 14px;
    }
    
    span {
      position: absolute;
      top: 0;
      right: 20px; 
      display: inline-block;
      font-size: 12px;
      color: #999;
    }
  }

  .content {
    width: 100%;
    height: 50px;
    line-height: 40px;
    background: #f4f4f4;
    @include border-bottom(#fff);

    span {
      padding-left: 20px;
      display: inline-block;
      font-size: 14px;
    }
  }
}

.interest {
  width: 100%;
  height: 160px;

  .interest-head {
    position: relative;
    display: flex;
    padding: 0 15px;
    height: 45px;

    i {
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 2px;
      height: 15px;
      background:#b93321; 
    }

    p {
      flex: 1;
      line-height: 45px;
      font-size: 14px;
      font-weight: bold;
    }

    span {
      flex: 0 0 24px;
      width: 24px;
      line-height: 45px;
      font-size: 12px;
      color: #b93321;
    }
  }
}
.interest .interest-book {
  display: flex;
  width: 100%;
  height: 115px;

  .book {
    flex: 1;
    padding: 0 15px;

    img {
      display: inline-block;
      width: 58px;
      height: 80px;
    }

    span {
      display: inline-block;
      width: 58px;
      height: 35px;
      text-align: center;
      line-height: 35px;
      word-break: break-all;
      overflow: hidden;
      text-overflow: ellipsis;
      font-size: 12px;
      color: #333;
    }
  }
}
</style>
