import Vue from "vue";
import Router from "vue-router";
import Choiceness from "./views/Choiceness";
import Details from "./views/Details";
import Section from "./views/Section";
import Read from "./views/Read";
import RankingBang from "./views/RankingBang";
import RankingBangA from "./views/RankingBangA";
import RankingBangB from "./views/RankingBangB";
import RankingBangC from "./views/RankingBangC";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "Choiceness",
      component: Choiceness,
      children: []
    },
    {
      path: "/vip",
      name: "Vip",
      component: () => import("./views/Vip.vue")
    },
    {
      path: "/classify",
      name: "Classify",
      component: () => import("./views/Classify.vue")
    },
    {
      path: "/seniority",
      name: "Seniority",
      component: () => import("./views/Seniority.vue")
    },
    {
      //详情页
      path: "/Details",
      name: "Details",
      component: Details,
      props: route => ({name: route.query.name}),
    },
    {
      //章节目录页
      path: "/Section",
      name: "Section",
      component: Section,
      props: route => ({id: route.query.id, name: route.query.name})
    },
    {
      //章节内容页
      path: "/Read",
      name: "Read",
      component: Read,
      props: route => ({item: route.query.item, id: route.query.id, name: route.query.name, datas: route.query.datas})
    },
    {
      //搜索页
      path: "/Seek",
      name: "Seek",
      component: () => import("./views/Seek.vue")
    },
    {
      // 搜索后的相应书籍页
      path: "/RelatedBook",
      name: "RelatedBook",
      props: route => ({name: route.query.name}),
      component: () => import("./views/RelatedBook.vue")
    },
    {
      // 排行榜页面
      path: "/RankingBang",
      name: "RankingBang",
      // component: () => import("./views/RankingBang.vue"),
      component: RankingBang,
      props: route => ({name: route.query.name, id: route.query.id, monthrank: route.query.monthrank, totalrank: route.query.totalrank,}),
      children: [
        {
          path: "/RankingBang/",
          name: "RankingBangA",
          props: route => ({name: route.query.name, id: route.query.id}),
          component: RankingBangA,
          // component: () => import("./views/RankingBangA.vue"),
        },
        {
          path: "/RankingBang/RankingBangB",
          name: "RankingBangB",
          props: route => ({name: route.query.name, monthrank: route.query.monthrank}),
          // component: () => import("./views/RankingBangB.vue"),
          component: RankingBangB,
        },
        {
          path: "/RankingBang/RankingBangC",
          name: "RankingBangC",
          props: route => ({name: route.query.name, totalrank: route.query.totalrank}),
          // component: () => import("./views/RankingBangC.vue"),
          component: RankingBangC,
        },
      ]
    },
    {
      //别人的排行榜
      path: "/Others",
      name: "Others",
      props: route => ({id: route.query.id}),
      component: () => import("./views/Others.vue")
    },
    {
      // 类别书籍页
      path: "/InnerClass",
      name: "InnerClass",
      props: route => ({name: route.query.name, direction :route.query.direction}),
      component: () => import("./views/InnerClass.vue")
    },
    {
      // 可能感兴趣的书籍的页面
      path: "/Interested",
      name: "Interested",
      props: route => ({type: route.query.type,  name: route.query.name, state: route.query.state, sex: route.query.sex}),
      component: () => import("./views/Interested.vue")
    },
  ],
  linkExactActiveClass: "active"
});
