<template>
  <div class="box" v-if="chapter">
    <div class="headline">
      <div class="go-back">
        <img src="../../public/img/detail-page03.png" alt>
        <span @click="upOne">返回</span>
      </div>
      <span class="book-details">优质书源</span>
      <div class="homepage">
        <img src="../../public/img/detail-page02.png" @click="homepage">
      </div>
    </div>
		<div class="box-chapter">
			<div class="catalog">
				<p>目录</p>
				<span>共{{chapter.data.chapters.length}}章</span>
				<p @click="invertedOrder">倒序</p>
			</div>
      <div class="section" v-for="(item, index) in chapter.data.chapters" :key="index" @click="skip(item)">
        <span class="sequence">{{index+1}}</span>
        <span>正文 {{item.title}}</span>
      </div>
		</div>
  </div>
</template>

<script>

export default {
  name: "Section",
  //name : 本书的数据
  props: ["id","name"],
  data() {
    return {
      //获取本书的小说源API(很多个)
      urls: "",
      //确认要获取的本书小说源API(混混小说网)
      url: "",
      //章节API
      sectionUrl: "",
      //小说章节
      chapter: ""
    }
  },
  watch: {
    url() {
      console.log("id==>",this.id)
      console.log("url==>",this.url)
      this.sectionUrl = "/api/atoc/" + this.url[0]._id + "?view=chapters"
      this.axios.get(this.sectionUrl).then(
      response => {
        console.log("小说章节",response)
        this.chapter = response
        }
      )
      // setTimeout(window.location.reload(),100)
    }
  },
	methods: {
		upOne() {
      console.log("name==>",this.name)
      this.$router.push({path: '/Details', query: {name: this.name}})
      // query:{name:item}
    },
    invertedOrder() {
      this.$set(this.chapter.data, this.chapter.data.chapters, this.chapter.data.chapters.reverse())
    },
    homepage() {
      this.$router.push({path: "/"})
    },
    skip(item) {
      // this.$router.push({path: "/Section", query: { id: this.id, name: this.name}})
      this.$router.push({path: "/Read", query: {item: item, id: this.id, name: this.name, datas: this.chapter.data.chapters}})
      
    },
  },
	created() {
    this.urls = "/api/atoc?view=summary&book=" + this.id
    this.axios.get(this.urls).then(
      response => {
        console.log(response)
        if(response.data.length > 1) {
          this.url = response.data.filter(function(element) {
          return  element.name === "混混小说网" ? element.name === "混混小说网" : element.name = "176小说"
          })
        }
        
      }
    )
  },
};
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
@import "../../public/sass/mixin";

.box {
  margin-top: -100px;
  position: relative;
  top: 0;
  left: 0;
  z-index: 100;
}
.box .headline {
  display: flex;
  width: 100%;
  height: 44px;
  background: #b93321;

  .go-back {
    flex: 0 0 60px;
    width: 60px;
    height: 100%;
    line-height: 44px;

    img {
      margin-left: 10px;
      width: 15px;
    }

    span {
      display: inline-block;
      margin-top: -3px;
      vertical-align: top;
      font-size: 12px;
      font-weight: 400;
      color: #fff;
    }
  }

  .book-details {
    flex: 1;
    text-align: center;
    line-height: 44px;
    font-size: 14px;
    color: #fff;
  }

  .homepage {
    flex: 0 0 50px;
    width: 50px;
    height: 100%;
    text-align: center;
    line-height: 44px;

    img {
      width: 15px;
    }
  }
}

.box-chapter {
  width: 100%;

  .catalog {
    display: flex;
    position: relative;
    padding: 15px 20px;
    width: calc(100% - 40px);
    height: 16px;
    @include border-bottom(#f8f8f8);
    color: #222;
    background: #fff;

    p {
      flex: 0 0 30px;
      display: inline-block;
      line-height: 16px;
      font-size: 14px;
      font-weight: bold;
    }

    span {
      padding-left: 10px;
      flex: 1 1 auto;
      line-height: 16px;
      font-size: 12px;
      color: #999;
    }
  }

  .section {
    padding-left: 20px;
    width: calc(100% - 20px);
    height: 50px;
    line-height: 50px;
    font-size: 12px;
    @include border-bottom(#f8f8f8);
    background: #fff;
    color: #999;

    span {
      display: inline-block;
    }

    span.sequence {
      margin-right: 5px;
    }
  } 
}


</style>
