<template>
  <div class="box">
    <div class="carousel">
      <ul ref="ul" @transitionend="cunrrent()">
        <li v-for="(img, i) in myImgArr" :key="i">
          <img :src="publicPath+'img/'+img">
        </li>
      </ul>
      <div class="index">
        <span
          v-for="(item, index) in imgArr"
          :key="index"
          :class="{active: index === myIndex}"
        ></span>
      </div>
    </div>

    <div class="box-btn">
      <router-link to tag="div">
        <img src="../../public/img/jx01.png">
        <span>书架</span>
      </router-link>
      <router-link to tag="div">
        <img src="../../public/img/jx02.png">
        <span>书单</span>
      </router-link>
      <router-link to tag="div">
        <img src="../../public/img/jx03.png">
        <span>漫画</span>
      </router-link>
      <router-link to tag="div">
        <img src="../../public/img/jx04.png">
        <span>我的</span>
      </router-link>
    </div>

    <div class="boy-fantasy" v-show="datum">
      <div class="headline">
        <i></i>
        {{hotBook}}
      </div>

      <div class="imgs" v-for="(item, index) in datum" :key="index" @click="routers(item)">
        <!-- <router-link :to="{path: '/Details', query:{name:item}}"> -->
        <img :src="setDatum(item.cover)">
        <div class="right">
          <div class="name">
            <span>{{item.title}}</span>
          </div>
          <div class="introduce">
            <span>{{item.shortIntro}}</span>
          </div>
          <div class="text">
            <span class="mold">{{item.majorCate}}</span> |
            <span class="moods">{{item.latelyFollower}}</span>人气
            <span class="small-mold">{{item.minorCate}}</span>
          </div>
        </div>
        <!-- </router-link> -->
      </div>

      <div class="more" @click="goInterested(1)">
        <div>
          <span>查看更多</span>
          <img src="../../public/img/detail-page01-2.png">
        </div>
      </div>
    </div>

    <div class="boy-entirely">
      <div class="headline">
        <i></i>
        {{entireBook}}
      </div>
      <div class="center">
        <div class="btn">
          <span v-for="(item, index) in spanData" :key="index" @click="goInnerClass(item)">{{item}}</span>
        </div>
        <div class="imgs" v-if="scienceData" @click="routers(scienceData[0])">
          <img :src="setDatum(scienceData[0].cover)">
          <div class="right">
            <div class="name">
              <span>{{scienceData[0].title}}</span>
            </div>
            <div class="introduce">
              <span>{{scienceData[0].shortIntro}}</span>
            </div>
            <div class="text">
              <span class="mold">{{scienceData[0].majorCate}}</span> |
              <span class="moods">{{scienceData[0].latelyFollower}}</span>人气
              <span class="small-mold">{{scienceData[0].minorCate}}</span>
            </div>
          </div>
        </div>
        <div class="big-box" v-show="scienceDatas">
          <div
            class="small-box"
            v-for="(item, index) in scienceDatas"
            :key="index"
            @click="routers(item)"
          >
            <div class="top">
              <span class="small-mold">{{item.minorCate}}</span>
              <span class="name">{{item.title}}</span>
              <span class="author">{{item.author}}</span>
            </div>
            <div class="bottom">
              <span>{{item.shortIntro}}</span>
            </div>
          </div>
        </div>
        <div class="more" @click="goInterested(2)">
          <div>
            <span>查看更多</span>
            <img src="../../public/img/detail-page01-2.png">
          </div>
        </div>
      </div>
    </div>

    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: "Choiceness",
  props: ["sex"],
  data() {
    return {
      cunrrentIndex: 0,
      publicPath: process.env.BASE_URL,
      imgArr: ["lbt01.png", "lbt02.png", "lbt03.png", "lbt04.png"],
      datum: "",
      name: "",
      otherItem: "",
      // 科幻完本数据第一条
      scienceData: "",
      // 科幻完本数据除去第一条
      scienceDatas: "",
      a: "",
    };
  },
  computed: {
    myImgArr() {
      let arr = this.imgArr;
      return [arr[arr.length - 1], ...arr, arr[0]];
    },
    hotBook() {
       let hotBook = ""
      if(this.sex === true) {
        hotBook = "男生热门"
      } else {
        hotBook = "女生热门"
      }
      return hotBook
    },
    entireBook() {
       let entireBook = ""
      if(this.sex === true) {
        entireBook = "男生完结"
      } else {
        entireBook = "女生完结"
      }
      return entireBook
    },
    spanData() {
      let spanData = ""
      if(this.sex === true) {
        spanData = ["都市", "仙侠", "玄幻", "科幻"]
      } else {
        spanData = ["现代言情", "女尊", "古代言情", "青春校园"]
      }
      return spanData
    },
    myIndex() {
      let result = this.cunrrentIndex
      if(result >= this.imgArr.length) {
        result = 0
      }
      return result
    }
  },
  watch: {
    sex() {
      let url = "";
      let url2 = "";
      if (this.sex === true) {
        url =
          "/api/book/by-categories?gender=male&type=hot&major=玄幻&minor=&start=0&limit=3";
        url2 =
          "/api/book/by-categories?gender=male&type=over&major=都市&minor=&start=0&limit=9";
      } else {
        url =
          "/api/book/by-categories?gender=female&type=hot&major=现代言情&minor=&start=0&limit=3";
        url2 =
          "/api/book/by-categories?gender=female&type=over&major=现代言情&minor=&start=0&limit=9";
      }
      //数据请求
      this.axios.get(url).then(
        response => {
          this.datum = response.data.books;
        },
        err => {
          alert(err);
        }
      );

      this.axios.get(url2).then(response => {
        this.scienceData = response.data.books.slice(0, 1);
        this.scienceDatas = response.data.books.slice(1, 4);
      });
    },
  },
  methods: {
    slide() {
        this.a  = setInterval(() => {
        this.cunrrentIndex ++
        this.$refs.ul.style.transition = "all 1s linear" 
        let offsetX = -(this.cunrrentIndex + 1) *375
        this.$refs.ul.style.transform = `translateX(${offsetX}px)`
      }, 3000)
    },
    cunrrent() {
      if(this.cunrrentIndex >= 4) {
        this.cunrrentIndex = 0
        this.$refs.ul.style.transition = "none"
        let offsetX = -(this.cunrrentIndex + 1) *375
        this.$refs.ul.style.transform = `translateX(${offsetX}px)`
        
      }
    },
    setDatum(url) {
      //图片路径处理
      let imgurl = url.replace("/agent/", "");
      imgurl = unescape(imgurl);
      return imgurl;
    },
    routers(item) {
      this.$router.push({ path: "/Details", query: { name: item } });
    },
    goInnerClass(item) {
      console.log(this.sex)
      if(this.sex === true) {
        this.$router.push({
        path: "/InnerClass",
        query: { name: item, direction: "male" }
        });
      } else if(this.sex === false) {
        this.$router.push({
        path: "/InnerClass",
        query: { name: item, direction: "female" }
        });
      }
      
    },
    goInterested(num) {
      let type = ""
      let state = ""
      let sex = ""
      if (num === 1) {
        if(this.hotBook == "男生热门") {
          type = "玄幻"
          state = "hot"
          sex = "male"
        } else {
          type = "现代言情"
          state = "hot"
          sex = "female"
        }
      } else if (num === 2) {
        if(this.hotBook == "男生完结") {
          type = "都市"
          state = "over"
          sex = "male"
        } else {
          type = "古代言情"
          state = "over"
          sex = "female"
        }
        
      }
      this.$router.push({
        path: "/Interested",
        query: { type: type, state: state, sex: sex}
      });
    }
  },
  beforeRouteLeave(to, from, next) {
    console.log("进入的路由==>", to);
    console.log("离开的路由==>", from);
    next(
      clearInterval(this.a)
    )
  },
  created() {
    let url = "";
    let url2 = "";
    if (this.sex === true) {
        url =
          "/api/book/by-categories?gender=male&type=hot&major=玄幻&minor=&start=0&limit=3";
        url2 =
          "/api/book/by-categories?gender=male&type=over&major=都市&minor=&start=0&limit=9";
      } else {
        url =
          "/api/book/by-categories?gender=female&type=hot&major=现代言情&minor=&start=0&limit=3";
        url2 =
          "/api/book/by-categories?gender=female&type=over&major=古代言情&minor=&start=0&limit=9";
      }

    //数据请求
    this.axios.get(url).then(
      response => {
        this.datum = response.data.books;
        // console.log(this.datum);
      },
      err => {
        alert(err);
      }
    );

    this.axios.get(url2).then(response => {
      this.scienceData = response.data.books.slice(0, 1);
      this.scienceDatas = response.data.books.slice(1, 4);
    });
    this.slide()
  },
};
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
