<template>
  <div class="box">
		<div class="headline">
    	<div class="go-back">
      <img src="../../public/img/detail-page03.png" alt>
      <span @click="upOne">返回</span>
    	</div>
    	<span class="book-details">{{name}}</span>
    	<div class="homepage">
      	<img src="../../public/img/detail-page02.png" @click="homepage">
    	</div>
  	</div>

		<div class="navigation">
				<span @click="week" :class="addActive===1? 'active':''">周榜</span>
				<span @click="month" :class="addActive===2? 'active':''">月榜</span>
				<span @click="total" :class="addActive===3? 'active':''">总榜</span>
		</div>

		<router-view></router-view>
	</div>
</template>

<script>
export default {
	name: "RankingBang",
	props: ["name", "id", "monthrank","totalrank"],
	data() {
		return {
			addActive: 1,
		}
	},
	methods: {
		upOne() {
      this.$router.push({path: '/seniority'})
    },
		homepage() {
      this.$router.push({path: "/"})
		},
		week() {
			this.$router.push({path: "/RankingBang/", query: {name: this.name, id: this.id}})
			this.addActive = 1
		},
		month() {
			this.$router.push({path: "/RankingBang/RankingBangB", query: {name: this.name, monthrank: this.monthrank}})
			console.log("111",this.monthrank)
			this.addActive = 2
		},
		total() {
			this.$router.push({path: "/RankingBang/RankingBangC", query: {name: this.name, totalrank: this.totalrank}})
			console.log("222",this.totalrank)
			this.addActive = 3
		}
	}
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
@import "../../public/sass/mixin";

.box {
	margin-top: -100px;
  z-index: 999;
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 120%;
  background: #fff;
}

.box .headline {
  display: flex;
  width: 100%;
  height: 44px;
  background: #b93321;

  .go-back {
    flex: 0 0 60px;
    width: 60px;
    height: 100%;
    line-height: 44px;

    img {
      margin-left: 10px;
      width: 15px;
    }

    span {
      display: inline-block;
      margin-top: -3px;
      vertical-align: top;
      font-size: 12px;
      font-weight: 400;
      color: #fff;
    }
  }

  .book-details {
    flex: 1;
    text-align: center;
    line-height: 44px;
    font-size: 14px;
		font-weight: bold;
    color: #fff;
  }

  .homepage {
    flex: 0 0 50px;
    width: 50px;
    height: 100%;
    text-align: center;
    line-height: 44px;

    img {
      width: 15px;
    }
  }
}

.box .navigation {
	display: flex;
	width: 100%;
	height: 56px;

		span {
			display: inline-block;
			flex: 1;
			height: 100%;
			line-height: 56px;
			text-align: center;
			font-size: 14px;
			color: #999;
		}

		span.active {
			border-bottom: 2px solid #b93321;
			box-sizing: border-box;
		}
	
}
</style>
