<template>
  <div class="books" v-if="datas" :class="id? '':'active'" ref="box">
    <div class="book" v-for="(item, index) in datas" :key="index" @click="toDetails(item)">
      <div class="img">
        <img :src="setDatum(item.cover)">
      </div>
      <dir class="right">
        <h4>{{item.title}}</h4>
        <div class="author">
          {{item.author}}
          <span>|</span>
          {{item.majorCate}}
        </div>
        <div class="synopsis">
          <span>{{item.shortIntro}}</span>
        </div>
        <div class="reader">
          <span>{{item.latelyFollower}}</span>人气
          <span class="upright">|</span>
          <span>{{item.retentionRatio}}%</span>读者留存
        </div>
      </dir>
    </div>
    <p v-show="!upload" @click="loadMore">加载更多书籍</p>
    <p v-show="upload">加载中...</p>
  </div>
</template>

<script>
export default {
  props: ["name", "id"],
  name: "RankingBangA",
  data() {
    return {
      datas: "",
      upload: false,
      start: 0,
      ending: 10
    };
  },
  methods: {
    setDatum(url) {
      //图片路径处理
      if (url) {
        let imgurl = url.replace("/agent/", "");
        imgurl = unescape(imgurl);
        return imgurl;
      }
    },
    loadMore() {
      this.upload = !this.upload;
      let url = "/api/ranking/" + this.id;
      this.axios.get(url).then(response => {
        this.datas = this.datas.concat(
          response.data.ranking.books.slice(this.start, this.ending)
        );
        this.start = this.ending;
        this.ending = this.ending + 10;
        this.upload = !this.upload;
      });
    },
    toDetails(item) {
      this.$router.push({ path: "/Details", query: { name: item } });
    }
  },
  created() {
    let url = "/api/ranking/" + this.id;
    console.log(url);
    this.axios.get(url).then(response => {
      this.datas = response.data.ranking.books.slice(0, 9);
      this.start = this.ending;
      this.ending = this.ending + 10;
      console.log(this.datas);
    });
  }
};
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
@import "../../public/sass/mixin";

.books {
  width: 100%;
  background: #f5f2ed;
}
.books.active {
  background: #fff;
}

.books .book {
  display: flex;
  margin-left: 15px;
  padding: 15px 15px 15px 0px;
  width: calc(100% - 30px);
  height: 75px;
  @include border-bottom(#ccc);

  .img {
    display: inline-block;
    flex: 0 0 60px;
    width: 60px;
    height: 75px;
    border: 1px solid #ebebeb;
    box-sizing: border-box;

    img {
      width: 100%;
      height: 100%;
    }
  }
}
.books .book .right {
  flex: 0 1 calc(100% - 75px);
  width: calc(100% - 75px);
  margin: 0;
  margin-left: 15px;
  padding: 0;

  h4 {
    height: 20px;
    line-height: 20px;
    font-size: 14px;
    font-weight: bold;
    color: #333 !important;
  }

  .author {
    height: 20px;
    line-height: 20px;
    font-size: 12px;
    color: #999;

    span {
      padding: 0 10px;
    }
  }

  .synopsis {
    height: 20px;
    line-height: 20px;
    font-size: 12px;
    color: #999;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;

    span {
      display: inline-block;
      width: 100%;
    }
  }

  .reader {
    height: 20px;
    line-height: 20px;
    font-size: 12px;
    color: #666;

    span {
      color: #b93321;
    }

    span.upright {
      color: #666;
      padding: 0 10px;
    }
  }
}

.books > p {
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  font-size: 12px;
  color: #999;
}
</style>
